<script>
    export let bind_value;
    export let label_value;
    export let style;

    function auto_grow(element) {
        element.target.style.height = "5px";
        element.target.style.height = (element.target.scrollHeight)+"px";
    }
</script>

<div class="form__group field" style={style} on:input>
    <textarea type="input" class="form__field" placeholder={label_value} name={label_value} on:input={auto_grow}
        id={label_value} bind:value={bind_value} required />
    <label for={label_value} class="form__label">{label_value}</label>
</div>

